import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { createBlogDesc, updateBlogDesc } from "../../reducers/blogReducer";
import AnimatedElement from "../../shared-components/AnimatedElement";
import FormElementComponent from "../../shared-components/FormElementComponent";

const BlogComment = (props) => {
  const blogId = props.blogId;
  const [showEditor, setShowEditor] = useState(
    props.blogDesc && props.blogDesc.blogDesc ? true : false
  );
  const { userdetails } = useSelector((state) => state.user);
  const formField = ["blogDesc-quill-optional"];
  const formInputValues = props.blogDesc ? props.blogDesc : {};

  const dispatch = useDispatch();
  const closeEditor = () => {
    props.blogDesc && props.blogDesc.blogDesc
      ? props.hideEditor()
      : setShowEditor(false);
  };

  const createData = async (formData) => {
    setShowEditor(false);
    const data = await dispatch(
      createBlogDesc({
        token: userdetails.token,
        blogDesc: formData.blogDesc,
        blogId,
      })
    );

    if (!data.error && data.payload !== undefined) {
      props.addComment(); //data.payload
    }
  };

  const updateData = async (formData) => {
    setShowEditor(false);
    const data = await dispatch(
      updateBlogDesc({
        token: userdetails.token,
        blogDesc: formData.blogDesc,
        blogId,
        blogDescId: props.blogDesc._id,
      })
    );

    if (!data.error && data.payload !== undefined) {
      props.addComment(); //data.payload
    }
  };

  return (
    <div className="pb-3 text-end s-color">
      {!showEditor && (
        <input
          type="text"
          className="w-100 s-back border-0 f-size-12 p-2 t-color-e5"
          placeholder="Enter Comment"
          onFocus={(e) => setShowEditor(true)}
          readOnly
        />
      )}
      {showEditor && (
        <>
          <AnimatedElement>
            <FormElementComponent
              formField={formField}
              formInputValues={formInputValues}
              modalHeading={
                formInputValues._id ? "Update Comment" : "Add Comment"
              }
              buttons={[
                {
                  type: "cancel",
                  title: "Cancel",
                },
                {
                  type: formInputValues._id ? "update" : "create",
                  title: formInputValues._id ? "Update" : "Create",
                },
              ]}
              closeModal={closeEditor}
              createData={createData}
              updateData={updateData}
              formType="modal"
            />
          </AnimatedElement>
        </>
      )}
    </div>
  );
};

export default BlogComment;
