import React, { useState } from "react";
import UserDataComponent from "../../shared-components/UserDataComponent";
import parse from "html-react-parser";
import BlogComment from "./BlogComment";
import { useDispatch, useSelector } from "react-redux";
import { deleteBlogDesc } from "../../reducers/blogReducer";
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
// import { faEdit, faTrash } from "@fortawesome/free-solid-svg-icons";
const BlogDesc = (props) => {
  const [showEditor, setShowEditor] = useState(false);
  const { userdetails } = useSelector((state) => state.user);
  const dispatch = useDispatch();
  const hideEditor = () => {
    setShowEditor(false);
  };
  const refreshBlogList = () => {
    setShowEditor(false);
    props.editComment();
  };
  const deleteBlogDescList = async () => {
    await dispatch(
      deleteBlogDesc({ token: userdetails.token, blogDescId: props.desc._id })
    );
    props.editComment();
  };
  return (
    <>
      <div className="t-color f-size-15">{parse(`${props.desc.blogDesc}`)}</div>
      <footer className="blockquote-footer d-flex f-size-12 ps-4">
        <cite title="Source Title">
          <span>
            {new Date(props.desc.updatedAt).toLocaleDateString("en-US", {
              day: "numeric",
              month: "long",
              year: "numeric",
            })}
          </span>{" "}
          | <UserDataComponent id={props.desc.userId} />
        </cite>
        {props.isCurrentUser && (
          <span className="f-size-12 ms-auto">
            <span
              className="secondary-link"
              onClick={(e) => setShowEditor(true)}
            >
              Edit
            </span>{" "}
            |{" "}
            <span
              className="secondary-link"
              onClick={(e) => deleteBlogDescList()}
            >
              Del
            </span>
          </span>
        )}
      </footer>
      {showEditor && (
        <BlogComment
          blogId={props.blogId}
          blogDesc={props.desc}
          index={props.index}
          addComment={refreshBlogList}
          hideEditor={hideEditor}
        />
      )}
      <div className="border-line-light mb-2"></div>
    </>
  );
};

export default BlogDesc;
