import React, { useEffect, useState } from "react";
import { Container } from "reactstrap";
import { getBlogDescList, getBlogList } from "../../reducers/blogReducer";
import { useDispatch, useSelector } from "react-redux";
import BlogDesc from "./BlogDesc";
import { useNavigate } from "react-router-dom";

import BlogComment from "./BlogComment";
import { getUesrId } from "../../reducers/authReducer";

const BlogDetails = (props) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { userdetails } = useSelector((state) => state.user);
  const [uid, setUid] = useState(null);
  const [blogListData, setBlogListData] = useState(null);
  const [blogDescListData, setBlogDescListData] = useState(null);
  const blogId = props.blogId;

  const getBlogs = async () => {
    const data = await dispatch(getBlogList({ blogId }));
    if (data.payload && !data.payload.error) {
      await setBlogListData(data.payload);
    } else {
      await setBlogListData(null);
    }
  };

  const getBlogDesc = async () => {
    const data = await dispatch(getBlogDescList({ blogId }));
    if (data.payload && !data.payload.error) {
      await setBlogDescListData(data.payload);
    } else {
      await setBlogDescListData(null);
    }
  };

  const getId = async () => {
    const data = await dispatch(getUesrId({ token: userdetails.token }));
    setUid(data.payload);
  };

  useEffect(() => {
    getBlogs();
    getBlogDesc();
    getId();
  }, [userdetails]);

  const refreshBlogList = () => {
    getBlogDesc();
  };

  return (
    <>
      <Container className="blogTech">
        <div className="blogTech-container">
          <div>
            <span
              className="d-none d-md-block p-color f-size-20 pt-2 cursor-pointer"
              onClick={() => navigate("/blog")}
            >
              &lt;&lt; Blogs
            </span>
          </div>
          <div className="flex-grow-1 p-md-4 t-back2">
            {blogListData &&
              blogListData.map((item, i) => (
                <blockquote className="blockquote mb-0 t-color" key={item._id}>
                  <h4>{item.blogQuestion}</h4>
                  {blogDescListData &&
                    blogDescListData.map((data, i) => (
                      <div key={data._id} className="ps-4">
                        <BlogDesc
                          desc={data}
                          index={i}
                          isCurrentUser={data.userId === uid}
                          editComment={refreshBlogList}
                        />
                      </div>
                    ))}
                  <BlogComment
                    blogId={item._id}
                    index={i}
                    blogDesc={null}
                    addComment={refreshBlogList}
                    hideEditor={null}
                  />
                </blockquote>
              ))}
          </div>
        </div>
      </Container>
    </>
  );
};

export default BlogDetails;
