import React from "react";
import Base from "../Base";
import BlogBanner from "../components/BlogView/BlogBanner";
import BlogTech from "../components/BlogView/BlogTech";
import ServiceCategories from "../components/HomeView/ServiceCategories";
import { useParams } from "react-router-dom";
import BlogDetails from "../components/BlogView/BlogDetails";

const Blog = () => {
  const { id } = useParams();
  return (
    <>
      <Base>
        <div className="container-fluid px-0">
          <BlogBanner />
          {id ? <BlogDetails blogId={id} /> : <BlogTech />}
          <ServiceCategories />
        </div>
      </Base>
    </>
  );
};

export default Blog;
