import React from "react";
import { Container } from "reactstrap";

const BlogBanner = () => {
  return (
    <>
      <Container fluid className="m-0 p-0">
        <div className="blogbanner-image d-block d-md-flex align-items-center">
          <img src="/images/blog-banner.jpg" alt="Blog" />
          <div className="t-color f-size-36 py-3 my-4 text-center flex-grow-1 px-3">
            Discover the best developer blogs on any tech
          </div>
        </div>
      </Container>
    </>
  );
};

export default BlogBanner;
