import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck, faEllipsisV } from "@fortawesome/free-solid-svg-icons";
import { deleteBlog, updateBlogList } from "../../reducers/blogReducer";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

const BlogQuestion = (props) => {
  const [editQuestion, setEditQuestion] = useState(false);
  const [showAction, setShowAction] = useState(false);
  const [question, setQuestion] = useState(props.question.blogQuestion);
  const [blogActive, setBlogActive] = useState(props.question.active);
  const blogId = props.question._id;
  const [updatedQuestion, setUpdatedQuestion] = useState(question);
  const { userdetails } = useSelector((state) => state.user);
  const token = userdetails ? userdetails.token : "";
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const updateChanges = async (token, updatedQuestion, blogId, blogActive) => {
    setQuestion(updatedQuestion);
    await dispatch(
      updateBlogList({ token, updatedQuestion, blogId, blogActive })
    );

    setEditQuestion(false);
  };
  const cancelUpdate = () => {
    setEditQuestion(false);
    setUpdatedQuestion(question);
  };
  const deleteHandler = async () => {
    await dispatch(deleteBlog({ token, blogId }));
    props.refreshBlogList();
  };

  const updateBlogActive = () => {
    updateChanges(token, updatedQuestion, blogId, !blogActive);
    setBlogActive(!blogActive);
  };
  return (
    <>
      <h5 className="d-flex justify-content-between fw-normal my-4 position-relative">
        {editQuestion ? (
          <>
            <span style={{ zIndex: "99" }} className="flex-grow-1 d-flex">
              <input
                type="text"
                defaultValue={question}
                onChange={(e) => setUpdatedQuestion(e.target.value)}
                className="flex-grow-1"
              />
              <FontAwesomeIcon
                icon={faCheck}
                onClick={(e) =>
                  updateChanges(token, updatedQuestion, blogId, blogActive)
                }
                className="mt-1 cursor-pointer"
                style={{ padding: "7px 7px" }}
              />
            </span>
            <div
              style={{
                position: "fixed",
                zIndex: "9",
                display: "block",
                height: "100vh",
                width: "100vw",
                top: 0,
                left: 0,
              }}
              onClick={() => cancelUpdate()}
            ></div>
          </>
        ) : (
          <>
            <span
              className="secondary-link flex-grow-1"
              onClick={(e) => navigate(`/blog/${blogId}`)}
            >
              {question}
            </span>
            {props.isCurrentUser && (
              <>
                <span
                  className={`${
                    !showAction
                      ? "d-none"
                      : "d-flex position-absolute s-back p-3"
                  } position-md-relative t-color d-md-flex gap-1 ms-auto  f-size-12 align-items-center`}
                  style={{ top: "3px", right: "20px", zIndex: 99 }}
                >
                  <span
                    className="secondary-link"
                    onClick={(e) => updateBlogActive()}
                  >
                    {blogActive ? "Active" : "Inactive"}
                  </span>
                  <span>|</span>
                  <span
                    className="secondary-link"
                    onClick={(e) => setEditQuestion(true)}
                  >
                    Edit
                  </span>
                  <span>|</span>
                  <span
                    className="secondary-link"
                    onClick={(e) => deleteHandler()}
                  >
                    Del
                  </span>
                </span>
                <span className="d-md-none">
                  <FontAwesomeIcon
                    icon={faEllipsisV}
                    onClick={(e) => setShowAction(!showAction)}
                    className="mt-1 cursor-pointer"
                    style={{ padding: "7px 7px" }}
                  />
                </span>
              </>
            )}
          </>
        )}
      </h5>
      {showAction && (
        <div
          className="position-fixed top-0 start-0 bottom-0 end-0"
          style={{ zIndex: 9 }}
          onClick={(e) => setShowAction(!showAction)}
        ></div>
      )}
    </>
  );
};

export default BlogQuestion;
