import React, { useEffect, useState } from "react";
import { Button, Col, Container, Row } from "reactstrap";
import { createBlogList, getBlogList } from "../../reducers/blogReducer";
import { useDispatch, useSelector } from "react-redux";

import BlogQuestion from "./BlogQuestion";
import { getUesrId } from "../../reducers/authReducer";
import FormElementComponent from "../../shared-components/FormElementComponent";

const BlogTech = () => {
  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [uid, setUid] = useState(null);
  const [addBlogModal, setAddBlogModal] = useState(false);
  const formFieldTypes = [
    "blogQuestion-text-require",
    "blogDesc-quill-optional",
  ];
  const [blogListData, setBlogListData] = useState(null);
  const formInputValues = {};

  const getBlogs = async () => {
    const data = await dispatch(getBlogList({ token: userdetails.token }));
    if (data.payload && !data.payload.error) {
      await setBlogListData(data.payload);
    } else {
      await setBlogListData(null);
    }
  };

  const getId = async () => {
    const data = await dispatch(getUesrId({ token: userdetails.token }));
    setUid(data.payload);
  };

  useEffect(() => {
    getBlogs();
    getId();
  }, [userdetails]);

  const openModal = () => {
    setAddBlogModal(true);
  };
  const closeModal = () => {
    setAddBlogModal(false);
  };

  const setOpenAddBlogModal = () => {
    getBlogs();
    closeModal();
  };

  const refreshBlogList = () => {
    getBlogs();
  };

  const createData = async (formData) => {
    const data = await dispatch(
      createBlogList({
        ...formData,
        token: userdetails.token,
        id: formInputValues.id ? formInputValues.id : null,
      })
    );
    if (data.payload) {
      setAddBlogModal(false);
      setOpenAddBlogModal();
    }
  };

  const updateData = () => {};

  return (
    <>
      <Container className="blogTech">
        <div className="blogTech-container">
          {userdetails && userdetails.token && (
            <Row>
              <Col className="text-end px-4">
                <Button
                  className="sp-btn-secondary rounded-0 px-5"
                  onClick={openModal}
                >
                  Create New Blog
                </Button>
              </Col>
            </Row>
          )}
          <div>
            <span className="d-none d-md-block p-color f-size-20 pt-2">
              Blogs
            </span>
          </div>
          <div className="flex-grow-1 p-md-4 t-back2">
            {blogListData &&
              blogListData.map((item, i) => (
                <blockquote className="blockquote mb-0 t-color" key={item._id}>
                  <BlogQuestion
                    question={item}
                    isCurrentUser={item.userId === uid}
                    refreshBlogList={refreshBlogList}
                  />
                </blockquote>
              ))}
          </div>
        </div>
        {addBlogModal && (
          <>
            {/* <CreateBlogComponentModal
            formField={formFieldTypes}
            formInputValues={formFieldValues}
            setAddBlogModal={setOpenAddBlogModal}
          /> */}
            <FormElementComponent
              formField={formFieldTypes}
              formInputValues={formInputValues}
              modalHeading={
                formInputValues.id ? "Update Blog" : "Create New Blog"
              }
              buttons={[
                {
                  type: "cancel",
                  title: "Cancel",
                },
                {
                  type: formInputValues.id ? "update" : "create",
                  title: formInputValues.id ? "Update" : "Create",
                },
              ]}
              closeModal={closeModal}
              createData={createData}
              updateData={updateData}
              formType="modal"
            />
          </>
        )}
      </Container>
    </>
  );
};

export default BlogTech;
